import { categories } from '@/lib/types';

export const metadata = {
    title: 'About',
};

export default function AboutPage() {
    return (
        <div className="container mx-auto px-4 md:px-6 py-12">
            <div className="max-w-4xl mx-auto">
                {/* Header */}
                <div className="mb-12 text-center">
                    <div className="mb-6">
                        <span className="text-6xl">💡</span>
                    </div>
                    <h1 className="text-4xl md:text-5xl font-bold mb-4">
                        생명과 데이터를 잇는 연구개발자
                    </h1>
                    <p className="text-lg text-[var(--foreground-secondary)]">
                        안녕하세요, happy-haru입니다.
                    </p>
                </div>

                {/* About Me */}
                <section className="mb-16">
                    <h2 className="text-2xl font-bold mb-6 flex items-center">
                        <span className="mr-3">🙋‍♂️</span>
                        About Me
                    </h2>
                    <div className="space-y-4 text-[var(--foreground-secondary)] leading-relaxed">
                        <p className="p-4 rounded-lg bg-[var(--surface)] border-l-4 border-[var(--primary)]">
                            🧪 생체신호 처리 및 텍스트 마이닝에 열정을 가진 데이터 기반 연구개발자입니다.
                        </p>
                        <p className="p-4 rounded-lg bg-[var(--surface)] border-l-4 border-[var(--secondary)]">
                            🫀 최근에는 심장 자기 신호 검출 및 분석 알고리즘 개발을 중심으로 헬스케어 데이터를 다루고 있습니다.
                        </p>
                        <p className="p-4 rounded-lg bg-[var(--surface)] border-l-4 border-[var(--accent)]">
                            📚 이전에는 텍스트 마이닝을 통해 유전자/질병/화학물질 관계 추출 프로젝트를 수행했습니다.
                        </p>
                        <p className="p-4 rounded-lg bg-[var(--surface)] border-l-4 border-[var(--primary)]">
                            🤝 문제 해결 중심의 사고와 협업을 통해 데이터로 인사이트를 찾는 과정을 즐깁니다.
                        </p>
                    </div>
                </section>

                {/* Current Focus */}
                <section className="mb-16">
                    <h2 className="text-2xl font-bold mb-6 flex items-center">
                        <span className="mr-3">✨</span>
                        요즘 이렇게 놀고(?) 있습니다
                    </h2>
                    <div className="glass rounded-xl p-6 md:p-8 border border-[var(--border)]">
                        <h3 className="text-xl font-bold mb-4">
                            🧠 LangChain으로 논문 수집하고, LangGraph로 관계 그래프 만들기!
                        </h3>
                        <p className="text-[var(--foreground-secondary)] leading-relaxed">
                            LangChain을 이용해 생물의학 논문, 데이터베이스, 그리고 기타 텍스트 데이터를 자동 수집 및 전처리하고,
                            LangGraph를 활용해 질병과 단백질, 유전자, 분자 간의 연결 고리를 시각적으로 분석하고자 합니다.
                            이걸 통해 질병 조기 진단에 유용한 바이오마커를 찾아내어 생명을 지키고자 합니다. 🎯
                        </p>
                    </div>
                </section>

                {/* Tech Stack */}
                <section className="mb-16">
                    <h2 className="text-2xl font-bold mb-6 flex items-center">
                        <span className="mr-3">🛠️</span>
                        Tech Stack
                    </h2>
                    <div className="grid grid-cols-2 md:grid-cols-3 gap-4">
                        {[
                            'Python',
                            'Biomedical Signal Processing',
                            'NLP',
                            'LangChain',
                            'BioBERT',
                            'Text Mining',
                            'MCG Analysis',
                            'Machine Learning',
                            'Data Visualization',
                        ].map((tech) => (
                            <div
                                key={tech}
                                className="p-4 rounded-lg bg-[var(--surface)] border border-[var(--border)] text-center font-medium hover:border-[var(--primary)] transition-colors"
                            >
                                {tech}
                            </div>
                        ))}
                    </div>
                </section>

                {/* Blog Categories */}
                <section className="mb-16">
                    <h2 className="text-2xl font-bold mb-6 flex items-center">
                        <span className="mr-3">📂</span>
                        Blog Categories
                    </h2>
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                        {Object.values(categories).map((category) => (
                            <div
                                key={category.id}
                                className="p-4 rounded-lg bg-[var(--surface)] border border-[var(--border)] hover:border-[var(--primary)] transition-colors"
                            >
                                <div className="flex items-center space-x-3">
                                    <span className="text-2xl">{category.emoji}</span>
                                    <div>
                                        <h3 className="font-bold">{category.name}</h3>
                                        <p className="text-sm text-[var(--foreground-secondary)]">
                                            {category.description}
                                        </p>
                                    </div>
                                </div>
                            </div>
                        ))}
                    </div>
                </section>

                {/* Motto */}
                <section>
                    <h2 className="text-2xl font-bold mb-6 flex items-center">
                        <span className="mr-3">💬</span>
                        Motto
                    </h2>
                    <blockquote className="glass rounded-xl p-8 border-l-4 border-[var(--primary)] italic text-lg">
                        "데이터 안에 답이 있다면, 저는 그걸 꺼내오는 사람입니다."
                    </blockquote>
                </section>
            </div>
        </div>
    );
}
