export interface Post {
    slug: string;
    title: string;
    date: string;
    description: string;
    category: string[];
    tags: string[];
    author: string;
    image?: string;
    content: string;
    readingTime?: string;
}

export interface PostMetadata {
    slug: string;
    title: string;
    date: string;
    description: string;
    category: string[];
    tags: string[];
    author: string;
    image?: string;
    readingTime?: string;
}

